import React from 'react';
import {Field, reduxForm} from 'redux-form';


import Slider,{Range} from 'rc-slider';
import Form from 'react-bootstrap/Form';
import FormControl from 'react-bootstrap/FormControl';
import Table from 'react-bootstrap/Table';


import FormGroup from 'react-bootstrap/FormGroup';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import 'rc-slider/assets/index.css';


class SettingsForm extends React.Component{
   
   componentDidMount(){

    
  }

  handleChange = newSliderValues => {


    this.props.handleChange(newSliderValues);

  };

	renderError({error, touched}){
		if(touched && error){
			return(
				<div className="error message">
					 <div className="text-danger">{error}</div>
				</div>
			);

		};
	}

	renderInput = ({input,label,meta,type},num) => {
	
		const className = `field ${meta.error && meta.touched ? 'error':''}`
		return (

 			<>
	          <Form.Group controlId={input.name}>
	          <Form.Label>{label}</Form.Label>
	          <Form.Control 
	            required {...input}	            
	            placeholder={"Enter your "+(label?label.toLowerCase():label)+" (separated by commas)" }
	            value={input.value}   
	            type={type}  
	            autoComplete="off"  
	          /> 
	          {this.renderError(meta)}   
	          </Form.Group>     
        	</>

			);
	}

	handleCBChange=(event)=>{

		console.log("handleCBChange:",event.target);
	

	}

renderCBnput = ({input,label,meta,type}) => {

		const className = `field ${meta.error && meta.touched ? 'error':''}`
		return (

 			<>
	          <Form.Group controlId={input.name}>	      
		         <Form.Check
		         	onChange={this.handleCBChange}
		         	{...input}	 
			        type={type}
			        label={label}			      
			      /> 
		          {this.renderError(meta)}   
	          </Form.Group>     
        	</>

			);
	}

	onSubmit=(formValues)=>{

		this.props.onSubmit(formValues);

	}
	renderResultsSection(){

		return(
		
				<pre>
				<code>
					{`{\n\t"id":"123",\n\t"details": "I am very unhappy with your service level.",\n\t"timestamp": "1586092601",\n\t"sentiments": 1,\n\t"bagofwords": ["service","level"],\n\t"ai": 1,\n\t"severity": 2\n}`}
				</code> 				
				</pre>
			
		);
	}
	renderAISection(){

		if(this.props.matrix)
		{
			const obj = JSON.parse(this.props.matrix);
			
			
			return(
	            <>              
	                <p>Confusion Matrix:</p>  
					<Table striped bordered hover size="sm">
						<thead> 
					 	<tr>
					 	  <td></td>
					      <td>Low Priority</td>
					      <td>Medium Priority</td>
					      <td>High Priority</td>
					    </tr>
					    </thead> 
					    <tbody> 
					    <tr>
					     <td>Low Priority</td>
					      <td>{obj[0][0]}</td>
					      <td>{obj[0][1]}</td>
					      <td>{obj[0][2]}</td>
					    </tr>
					    <tr>
					    <td>Medium Priority</td>
					      <td>{obj[1][0]}</td>
					      <td>{obj[1][1]}</td>
					      <td>{obj[1][2]}</td>
					    </tr>
					    <tr>
					     <td>High Priority</td>
					      <td>{obj[2][0]}</td>
					      <td>{obj[2][1]}</td>
					      <td>{obj[2][2]}</td>
					    </tr>
					    </tbody> 
					</Table>
	    				<Field 
						name="includeAI" 
						component={(field)=>this.renderCBnput(field)}
						label="Include in evaluation"
						type="checkbox"
						/>
	            </>
      		);
		}
		else
		{

			return(         
              
               <p>You do not have an existing model.</p>    				
			         
      		);
		}
    
}
	render(){

		return (

			<form 
				className="ui form error" 
				onSubmit={this.props.handleSubmit(this.onSubmit)} 
			>
	          <Row noGutters="true">
	            <Col> 
	            </Col> 
	            <Col lg={11} xs={11}> 
	             <div className="text-success">{this.props.message} </div>
	             <div className="text-danger"> {this.props.errors} </div>
	            </Col>
	            <Col> 
	            </Col> 
	          </Row> 
	          <Row noGutters="true">
	            <Col> 
	            </Col> 
	            <Col lg={11} xs={11}> 
				<div className="text-info mt-4 mb-3"><h5>API - EndPoint:</h5></div>
				<p>
					http://localhost:9000/{this.props.hash}/check
				</p>
	

				<div className="text-info mt-5 mb-3"><h5>API - Requirement (Form Data):</h5></div>
				<p>
					Post form-data to the API endpoint with following requirement:
				</p>
					<table className="responsive" border="1">
					  <thead>
					    <tr>
					      <th>Form Field Name</th>
					      <th>Form Field Type</th>
					      <th>Description</th>
					    </tr>
					  </thead>
					  <tbody>
					    <tr>
					      <td>id</td>
					      <td>Text</td>
					      <td>Unique identifier in your existing ticketing system</td>
					    </tr>
					    <tr>
					      <td>details</td>
					      <td>Text</td>
					      <td>Textual content of your tickets.</td>
					    </tr>
					  </tbody>
					  <tfoot>
					    <tr>
					      <td>timestamp</td>
					      <td>Text</td>
					      <td>Timestamp of ticket in Unix Timestamp(eg. 1586092601)</td>
					    </tr>
					  </tfoot>
					</table>	
		  		</Col>
	            <Col> 
	            </Col> 
	          </Row> 

		  		
          		<Row noGutters="true">
	            <Col> 
	            </Col> 
	            <Col lg={11} xs={11}> 
	            <div className="text-info mt-5 mb-3"><h5>Expected Results:</h5></div>
				<p>
					Example:
				</p>
	              {this.renderResultsSection()}
				<p>
					Information:
				</p>
				<table className="responsive" border="1">
					  <thead>
					    <tr>
					      <th>Key</th>			
					      <th>Description</th>
					    </tr>
					  </thead>
					  <tbody>
					    <tr>
					      <td>id</td>
			
					      <td>Unique identifier in your existing ticketing system</td>
					    </tr>
					    <tr>
					      <td>details</td>
				
					      <td>Textual content of your tickets.</td>
					    </tr>
					  </tbody>
					  <tfoot>
					    <tr>
					      <td>timestamp</td>
				
					      <td>Timestamp of ticket in Unix Timestamp(eg. 1586092601)</td>
					    </tr>
					    <tr>
					      <td>sentiments</td>
				
					      <td>Range of values from 0 to 4. The lower it is, the more negative is the sentiments</td>
					    </tr>
						<tr>
					      <td>bagofwords</td>
				
					      <td>These are words detected by the system</td>
					    </tr>
					    <tr>
					      <td>ai</td>
				
					      <td>If you have a trained model, you may include it in the evaluation</td>
					    </tr>					    			    					    
					  </tfoot>
					</table>	
	           </Col>
	            <Col> 
	            </Col> 
	          </Row> 
				
					
			 <Row noGutters="true">
	            <Col> 
	            </Col> 
	            <Col lg={11} xs={11}> 
			 	<div className="text-info mt-5 mb-3"><h5>Business Rules - Bag of Words:</h5></div>
                      
						<Field 
							name="p3words" 
							component={(field)=>this.renderInput(field)}
							label="High Priority Words"
							type="text"
							/>
						<Field 
							name="p2words" 
							component={(field)=>this.renderInput(field)}
							type="text"
							label="Medium Priority Words"/>
						<Field 
							name="p1words" 
							component={(field)=>this.renderInput(field)}
							type="text"
							label="Low Priority Words"/>
		 				
						<div className="text-info mt-5 mb-3"><h5>Business Rules - Sentiments:</h5></div>
						<div className="form-group mx-5">                      
		                	<Range 
		                      onChange={this.handleChange}
		                      min={0} 
		                      max={4} 
		                      value={this.props.sliderValues} 
		                      defaultValue={this.props.sliderValues} 
		                      marks={{ 0: "Very Negative", 1: "|", 2: "|", 3: "|", 4: "Very Positive" }}
		                    />           
		                </div> 
		                
						
 					</Col>
	            <Col> 
	            </Col> 
	          </Row> 
			 <Row noGutters="true">
	            <Col> 
	            </Col> 
	            <Col lg={11} xs={11}> 	          
			 	<div className="text-info mt-5 mb-3"><h5>Business Rules - Artificial Intelligence:</h5></div>
                  	
				 {this.renderAISection()}             	
					</Col>
	            <Col> 
	            </Col> 
	          </Row> 	
	           <Row> 	
			            <Col> 
	            </Col> 
	            <Col lg={11} xs={11}> 	
				<button className="btn btn-primary float-right">Save</button>
					</Col>
	            <Col> 
	            </Col> 
	          </Row> 
			</form>
			);
	
	}
};


const validateInput = (formValues) =>{
  const errors = {};

  if(!formValues.p1words){
      errors.p1words ='You must enter at least one word';
  }

  if(!formValues.p2words){ 
      errors.p2words ='You must enter at least one word';
  }
  if(!formValues.p3words){ 
      errors.p3words ='You must enter at least one word';
  }

  return errors;

};



export default reduxForm({
	form: 'settingsForm',
	validate : validateInput
})(SettingsForm);

