import {GET_SETTING,SAVE_SETTING,SAVE_AI_SETTING,EDIT_SLIDER} from '../actions/types'

const INITIAL_STATE={

	sliderValues: [1,3]



};

export default (state=INITIAL_STATE,action) => { 



	switch(action.type){

		case GET_SETTING:
			
			return {...state, 
				settings:action.payload,
				sliderValues:[action.payload.marker1,action.payload.marker2],
				hash:action.payload.hash,
				confMatrix:action.payload.confMatrix,
				classReport:action.payload.classReport,
				message:action.payload.message,
				errors:action.payload.error

			}
						
		case SAVE_SETTING:
			return {...state, 
				sliderValues:action.payload.sliderValues,	
				settings:action.payload.settings,
				message:action.payload.message.msg,
				errors:action.payload.error
			};
		case SAVE_AI_SETTING:
			return {...state, 

				message:action.payload.message,
				errors:action.payload.error

			};	
		case EDIT_SLIDER:
			return {...state, 
				settings:action.payload.settings,
				sliderValues:action.payload.sliderValues			
			};
		default:
			return state;
	}
}