import React from 'react';

import {Router, Route, Switch} from 'react-router-dom';
import { withRouter } from "react-router";


import Benefits from './traige/Benefits';
import Contact from './traige/Contact';
import Pricing from './traige/Pricing';
import Settings4 from './traige/Settings4'; 
import Login from './traige/Login';
import RegisterBootstrapForm from './traige/RegisterBootstrapForm'; 


import Header from './Header';
import history from '../history';
import Container from 'react-bootstrap/Container';




const App = () => {

const HeaderWithRouter = withRouter(Header);


	return (
    
 
	<Container >
		<Router history={history}>
			<div>			
			
				<HeaderWithRouter/>
				<Switch>
				<Route path="/" exact component={Benefits} />
				<Route path="/benefits"  component={Benefits} />
				<Route path="/contact"  component={Contact} />				
				<Route path="/pricing"  component={Pricing} />
				<Route path="/settings4"  component={Settings4} />
				<Route path="/login"  component={Login} />		
				<Route path="/registerBSF"  component={RegisterBootstrapForm} />
				</Switch>
			</div>
			</Router>

	</Container>

	
	);
	
};


export default  App;
