
import React,{useContext, useState, useEffect } from 'react';
import {Link} from 'react-router-dom';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import FormControl from 'react-bootstrap/FormControl';
import AuthContext from '../context/AuthContext';
import { Triangle,ArrowRight } from 'react-bootstrap-icons';
import history from '../history';


const Header = (props) =>{

  const authContext = useContext(AuthContext); 
  const [logged, setLogged] =useState(false);
  const [userId, setUserId] =useState(null);

	const { location } = props;

  const home_url = "/";
  const contact_url = "/contact";
  const benefits_url = "/benefits";
  const pricing_url = "/pricing";
  const settings4_url = "/settings4";
  const login_url = "/login";
  const registerRDF_url = "/registerRDF";
  const logout_url = "/logout";

 

  const logout = () =>{


    authContext.isAuth=false;


    localStorage.setItem('isSignedIn', false);
    localStorage.removeItem('userId');
    setLogged(false)
    setUserId(null)
    history.push('/login');
   

  }


  useEffect(() => {

        const isIn = localStorage.getItem('isSignedIn');
        const uid = localStorage.getItem('userId');
        setLogged(isIn)
        setUserId(uid)


  }, [logged,authContext.isAuth]);


  const MyNavBar=()=>{



    if(logged=='true')
    { 
      return (

          <>
            <Nav.Link href={settings4_url}>Settings</Nav.Link>
            <Nav.Link  href="#" onSelect={logout} >
                  Logout
            </Nav.Link>
           </>
        );
    }
    else
    {
     
      return (
          <>
       
                <Nav.Link href={benefits_url}>Benefits</Nav.Link>
                <Nav.Link href={contact_url}>Contacts</Nav.Link>
                <Nav.Link href={pricing_url}>Pricing</Nav.Link>
                <Nav.Link href={login_url}>Login</Nav.Link>
                                
          </>
      );
     }

    
  }

	return (
<>
    <Navbar bg="info" expand="lg">
    <Navbar.Brand href="#home">          
      <Triangle size={30}/>
          {' '}
          TRAIGE   
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
             <Navbar.Collapse id="basic-navbar-nav">  
                 <Nav className="mr-auto" activeKey={location.pathname}> 
                <MyNavBar/>                  
              </Nav>
      </Navbar.Collapse> 
    </Navbar>
    </>
		);
	
};


export default Header;
