import React from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import FormControl from 'react-bootstrap/FormControl';
import Form from 'react-bootstrap/Form';
import FormGroup from 'react-bootstrap/FormGroup';
import Button from 'react-bootstrap/Button';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import {Field, reduxForm} from 'redux-form';

import {register} from '../../actions';
import * as common from './forms/Common';

class RegisterBootstrapForm extends React.Component{

   

 
renderError({error, touched}){
    if(touched && error){

      return(
        <div className="error message">
          <div className="text-danger">{error}</div>
        </div>

      );

    }
  }

  renderInput = ({input,label,type,meta}) => {
    const className = `field ${meta.error && meta.touched ? 'error':''}`
    return (
        <>
          <Form.Group controlId={input.name}>
          <Form.Label>{label}</Form.Label>
          <Form.Control 
            required {...input} 
            autoComplete="off"  
            placeholder={"Enter your "+label.toLowerCase() }
            value={input.value}   
            type={type}  
          /> 
          {this.renderError(meta)}   
          </Form.Group>
     
        </>
      );
  }


   renderEmailField = (field) => (
        <Form.Group controlId="email2">
          <Form.Label>Email address</Form.Label>
          <Form.Control required type="email" placeholder="Enter email" />                   
        </Form.Group>
    );


    renderPasswordField = (field) => (
                  <Form.Group controlId="password2">
                    <Form.Label>Password</Form.Label>
                    <Form.Control 
                    required type="password" 
                    placeholder="Password" />
                  </Form.Group>
    );


    onSubmit = (formValues) =>{

        this.props.register(formValues);



    }

    render(){
        return (
        <>
          <Row noGutters="true"  className="mt-4">            
            <Col> 
            </Col> 
            <Col lg={6} xs={11}> 
             <h5> Register </h5>

               <form 
                  className="ui form error" 
                  onSubmit={this.props.handleSubmit(this.onSubmit)} 
                >
                   <Field 
                   label="Email" 
                   name="email" 
                   type="email"
                   component={this.renderInput} 
                 
                   />  
                    <Field 
                   label="Password" 
                   name="password" 
                   type="password"
                   component={this.renderInput} 
                   />   

                    <Field 
                   label="Confirm Password" 
                   name="password2" 
                   type="password"
                   component={this.renderInput} 
                   />       

                
                      <button className="btn btn-primary float-right" >Register</button>

                    <a className="float-left" href="/login">Cancel</a>
               
                </form>


            </Col>
            <Col>
            </Col>
          </Row>    
          <Row noGutters="true">
            <Col> 
            </Col> 
            <Col lg={6} xs={11}> 
             <div className="text-success">{this.props.message} </div>
             <div className="text-danger"> {this.props.errors} </div>
          </Col>
                      <Col> 
            </Col> 
          </Row> 

          </>    
        );
    }
    
};

const mapStateToProps = (state) =>{


    return {
        message: state.auth.message ,       
        errors: state.auth.errors

    };
};



const validateInput = (formValues) =>{
  const errors = {};


  if(!formValues.email){
      errors.email ='you must enter a email';
  }else{
      errors.email = common.validEmailRegex.test(formValues.email)
      ? ''
      : 'Please enter a valid email address.';
  }

  if(!formValues.password){ 
      errors.password ='you must enter a password';
  }else{
      errors.password = 
      formValues.password.length > 16 || formValues.password.length <8
        ? 'Please enter a password between 8 to 16 characters.'
        : '';
  }

  if(!formValues.password2){
      errors.password2 ='you must enter the password again';
  }else{

      errors.password2 = 
      formValues.password !== formValues.password2
      ? 'Please make sure the passwords are the same.'
      : '';   
  }  
  return errors;

};


RegisterBootstrapForm = connect(
    mapStateToProps,
    {register}
)(RegisterBootstrapForm);

export default reduxForm({
    form: 'authform', 
    validate : validateInput 
})(RegisterBootstrapForm);

