import _ from 'lodash';
import React from 'react';
import {connect} from 'react-redux';
import Table from 'react-bootstrap/Table';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import {getSettings,saveSettings,editSlider,saveAISettings} from '../../actions';
import SettingsForm from './forms/SettingsForm';
import AIForm from './forms/AIForm';

import AuthContext from '../../context/AuthContext';

import history from '../../history';

class Settings4 extends React.Component{

static contextType = AuthContext; 
state ={}
  
  componentDidMount(){
   
    const isSignedIn = localStorage.getItem('isSignedIn');
    const userId = localStorage.getItem('userId');
    
    this.setState({ isSignedIn, userId });


    if(isSignedIn=='true')
    {
      this.props.getSettings(userId);
    }

    
  }

  handleChange = sliderValues => {


 
    this.props.editSlider(sliderValues,this.props.settings);



  };

  onSubmit=(formValues)=>{
    // console.log("s4handleChange:"+formValues.marker1)
    // console.log("s4handleChange:"+formValues.marker2)
    const userId = this.context.userId;
    console.log("s4 submit state b4:"+this.props.sliderValues)
    this.props.saveSettings(formValues,this.props.sliderValues,this.state.userId);

  }
  onSubmitAI=(formFile)=>{

    const userId = this.context.userId;

    this.props.saveAISettings(this.state.userId,formFile);

  }
  renderAdmin(){


if(this.state.isSignedIn=='true')
    {
      return(
            <>
              
                <SettingsForm 
                initialValues={this.props.settings} 
                sliderValues={this.props.sliderValues}
                hash={this.props.hash}
                onSubmit={this.onSubmit} 
                handleChange={this.handleChange}
                message={this.props.message}
                errors={this.props.errors}
                report={this.props.classReport}
                matrix={this.props.confMatrix}
                />
                <AIForm 
                initialValues={this.props.AIsettings} 
                onSubmitAI={this.onSubmitAI} 
                message={this.props.message}
                errors={this.props.errors}
                />
            </>
      );
    }else{
      return(
            <div> Unauthorized Access. Click <a href="/login"> here</a> to log in
            </div>
      );

    }

  }

  render(){

    return (

     
          <>
           
              <Row noGutters="true" className="mt-4" >             
              <Col> 
              </Col> 
              <Col lg={10} xs={11}> 
               <h5> Settings </h5>

               {this.renderAdmin()}
               </Col> 
              <Col> 
              </Col> 
              </Row > 
             
          </>

       
  
      );
  
  }
  
};



const mapStateToProps = (state) => ({ 
    settings: state.traige.settings,
    AIsettings: state.traige.AIsettings,
    sliderValues: state.traige.sliderValues,
    hash:state.traige.hash,
    message: state.traige.message,
    errors: state.traige.errors,
    confMatrix: state.traige.confMatrix,
    classReport: state.traige.classReport
 
});


export default connect(mapStateToProps,{
 getSettings,saveSettings,editSlider,saveAISettings 
},
null,
  { pure: false })(Settings4);



