import {SIGN_IN,SIGN_OUT,REGISTER} from '../actions/types'

const INITIAL_STATE={
	isSignedIn: false,
	userId: null,
	validated : false,
	message: null,
	errors:null,

};

export default (state=INITIAL_STATE,action) => { 



	switch(action.type){
  	
		case SIGN_IN:
			return {...state, 
				isSignedIn:true,
				userId:action.payload.uid,
				message:action.payload.message,
				errors:action.payload.error
			};
		case SIGN_OUT:
			return {...state, isSignedIn:false,userId:null};
		case REGISTER:
			return {...state, 
				isSignedIn:false,
				userId:null,
				message:action.payload.message,
				errors:action.payload.error
			};	
		default:
			return state;
	}
}