import React from 'react';


const Context =  React.createContext({
    isAuth: false,
    userId :null
	});


export class AuthStore extends React.Component{

	state = { isAuth: false, userId :null };

	onAuthChange = (isAuth, userId )=> {
		this.setState({isAuth: isAuth, userId :userId });
	};


	render() {
    return (
      <Context.Provider
        value={{ ...this.state, onAuthChange: this.onAuthChange }}>
        {this.props.children}
      </Context.Provider>
    );
	
	}
}

export default Context;


