import _ from 'lodash';
import axios from 'axios';
import history from '../history';
import {reset} from 'redux-form';
import {
	REGISTER,
	SIGN_IN,
	SIGN_OUT,
	GET_SETTING,
	SAVE_SETTING,
	SAVE_AI_SETTING,
	EDIT_SLIDER,
	CLEAR_FORM
} from './types';


import base from '../api/base';



export const getSettings =  (userId) => async (dispatch) => {

	const response =  await base.get(`/settings/${userId}`)
	.then(function (response) {
    	dispatch({
		type: GET_SETTING,
		payload: response.data
		})
  	})
	.catch(function (error) {
	    if (error.response) {

	    } else if (error.request) {

	    } else {

	    }    	
    	dispatch({
		type: GET_SETTING,
		payload: "error"
		})
  	});


}
export const saveSettings =  (formValues,sliderValues,userId) => async (dispatch) => {

	const params = new URLSearchParams();
	//params.append('marker1', formValues.marker1);
	//params.append('marker2', formValues.marker2);
	params.append('marker1', sliderValues[0]);
	params.append('marker2', sliderValues[1]);
	params.append('p1words', formValues.p1words);
	params.append('p2words', formValues.p2words);
	params.append('p3words', formValues.p3words);
	params.append('includeAI', formValues.includeAI);
	params.append('id', userId);
   
	const response =  await base.post('/settings/update',params); 
	response.data["sliderValues"]=sliderValues

	dispatch({
		type: SAVE_SETTING,
		payload: response.data
	})		

}

export const saveAISettings =  (userId,formFile) => async (dispatch) => {

	const formData = new FormData();
    formData.append('file',formFile)
	formData.append('id', userId);	

    const config = {
        enctype:"multipart/form-data"
    }

	const response =  await base.post('/upload',formData,config); 

	dispatch({
		type: SAVE_AI_SETTING,
		payload: response.data
	
	})		

}

export const editSlider =  (sliderValues,settings) => async (dispatch) => {

 //    settings.marker1=sliderValues[0]
	// settings.marker2=sliderValues[1]
    const combined = {settings,sliderValues};
	dispatch({
		type: EDIT_SLIDER,
		payload: combined
	})	

}
export const register =  (formValues) => async (dispatch) => {
	
	const em = formValues.email
	const pw = formValues.password
	const input = {
		"em":em,
		"pw":pw
	}
	const response =  await base.post('/register',input)
	.then(function (response) {

		dispatch({
		type: REGISTER,
		payload: response.data
		})	
		dispatch(reset('authform'));     
  	})
	.catch(function (error) {
		let data={}
		let errorMsg = null
	    if (error.response) {
	      errorMsg="Error";

	    } else if (error.request) {

	      errorMsg="Server unavailable";
	    } else {

	      errorMsg="Serious Error";
	    }

    	data.error = errorMsg;

    	dispatch({
			type: REGISTER,
			payload: data
		})
    	
  	});    
	

}

export const signIn =  (formValues,setAuth) => async (dispatch) => {

	const em = formValues.email
	const pw = formValues.password
	const input = {
		"em":em,
		"pw":pw
	}
	const response =  await base.post('/login',input)
	.then(function (response) {

	    setAuth(true,response.data.uid);

		dispatch({
			type: SIGN_IN,
			payload: response.data
		})		

		dispatch(reset('loginform'));     

		history.push('/settings4');
  	})
	.catch(function (error) {
		let data={}
		let errorMsg = null
	    if (error.response) {

	      errorMsg="Error";

	    } else if (error.request) {

	      errorMsg="Server unavailable";
	    } else {

	      errorMsg="Serious Error";
	    }
    	data.error = errorMsg;

    	dispatch({
			type: SIGN_IN,
			payload: data
		})
    	
  	});    

}
export const signOut =  () => {	

	return {
		type: SIGN_OUT
	};
}

