import React from 'react';
import {Field, reduxForm} from 'redux-form';


import Slider,{Range} from 'rc-slider';
import Form from 'react-bootstrap/Form';
import Table from 'react-bootstrap/Table';
import FormControl from 'react-bootstrap/FormControl';
import FormGroup from 'react-bootstrap/FormGroup';
import FormFile from 'react-bootstrap/FormFile';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import 'rc-slider/assets/index.css';

class AIForm extends React.Component{
   
   state = 
   	{	
   		"file": null,
   	  	"name": null

    }




	renderError({error, touched}){
		if(touched && error){
			return(
				<div className="error message">
					 <div className="text-danger">{error}</div>
				</div>
			);

		};
	}
	
	handleFileChange=(event)=>{

		this.setState({"file":event.target.files[0]})
		this.setState({"name":event.target.files[0].name})

	}
	renderFileInput = ({input,label,meta,type},ph) => {

		
		const className = `field ${meta.error && meta.touched ? 'error':''}`
		return (

 			<>

	          <Form.Group controlId={input.name}>	
				    <Form.File id={input.name} custom>
				      <Form.File.Input  
				   		onChange={this.handleFileChange}
				  
				      />
				      <Form.File.Label data-browse="Browse">
				        {this.state.name?this.state.name:label}
				      </Form.File.Label>
				     </Form.File>
	          </Form.Group>     

        	</>

			);
	}
renderFileInput2 = (field) => {

		const className = `field ${field.meta.error && field.meta.touched ? 'error':''}`
		return (

 			<>
	          <Form.Group controlId={field.input.name}>	
				    <Form.File id={field.input.name} custom>
				      <Form.File.Input 

				      />
				      <Form.File.Label data-browse="Browse">
				        {field.label} 
				      </Form.File.Label>
				     </Form.File>
	          </Form.Group>     

        	</>

			);
	}
 
	onSubmit=(formValues)=>{

		this.props.onSubmitAI(this.state.file);

	}

	render(){

		return (



			<form 
				className="ui form error" 
				onSubmit={this.props.handleSubmit(this.onSubmit)} 
			>
	          <Row noGutters="true" className="mt-5 mb-3">
	            <Col> 
	            </Col> 
	            <Col lg={11} xs={11}> 
	            </Col>
	            <Col> 
	            </Col> 
	          </Row> 

	          <Row noGutters="true">
	            <Col> 
	            </Col> 
	            <Col lg={11} xs={11}> 	  
			  		<div className="text-info mt-5 mb-3"><h5>Model Training:</h5></div>
											
					<Field 
						name="file" 
						component={(field)=>this.renderFileInput(field)}
						label="Training Data Upload"				
						type="file"
						/>	
					 </Col>
	            <Col> 
	            </Col> 






	          </Row> 

	          <Row noGutters="true">
	            <Col> 
	            </Col> 
	            <Col lg={11} xs={11}> 	
	           <p>Instructions:</p>  
			   
			   <p>
			   1. Prepare you data by extracting the textual content of your tickets and place it under 'sentence' column
			   </p>  
			   <p>
			  	2. Labelled your data based on 3 levels of severity (0,1,2) under 'priority' column, with 2 being the most serious.
			  	</p>  
			  <p>
			  3. Ensure data is in comma-delimited format and in a CSV file.
			  </p>
	          <Table striped bordered hover size="sm">
						<thead> 
					 	<tr>
					 	  <td><b>sentence</b></td>
					      <td><b>priority</b></td>
					    </tr>
					    </thead> 
					    <tbody> 
					    <tr>
					     <td>The system crashed a number of times.</td>
					      <td>1</td>
					    </tr>
					    <tr>
					     <td>...</td>
					      <td>...</td>
					    </tr>					  
					    </tbody> 
					</Table>

				</Col>
	            <Col> 
	            </Col> 
	          </Row> 


 				<Row noGutters="true">
	            <Col> 
	            </Col> 
	            <Col lg={11} xs={11}> 	
	          
					<button className="btn btn-primary float-right">Upload</button>
				    </Col>
	            <Col> 
	            </Col> 
	          </Row> 
			</form>
			);
	
	}
};


const validateInput = (formValues) =>{
  const errors = {};

  return errors;

};


//similar to connect founction
export default reduxForm({
	form: 'aiForm',
	validate : validateInput
})(AIForm);

